.footer {
    background-color: #000;
    padding: 89px 0 0;
    color: #EEEEEF;
    @include screen($tablet) {
        padding-top: 2rem;
        .social-list {
            margin-top: .5rem
        }
    }
    @include screen($mobile) {
        padding-top: 1.5rem;
        .social-list {
            margin-top: 0
        }
    }
    .container {
        display: flex;
        flex-wrap: wrap;
        gap: 2rem;
        justify-content: space-between;
        @include screen(768px) {
            gap: 26px;
        }
    }

    a {
        color: #EEEEEF;


    }

    &__menu {
        columns: 2;
        column-gap: 70px;
        margin: 0 64px 0 auto;
        li {
            margin-bottom: 8px;
        }
        @include screen($tablet) {
            order: 2;
            margin: 6px 0;
            column-gap: normal;
            width: 100%;
        }
        @include screen($mobile) {
             columns: 1;
             column-gap: 0;
             margin: 0;
             & li:nth-child(4) {
                margin-bottom: 2rem;
             }
        }
    }
    &__copyright {
        width: 100%;
        border-top: 1px solid rgba(168, 170, 174, 0.32);
        display: flex;
        justify-content: space-between;
        padding-top: 25px;
        padding-bottom: 7px;
        @include screen($tablet) {
            order: 3;
            padding-top: 16px;
            padding-left: 12px;
            padding-bottom: 16px;
        }
        @include screen($mobile) {
            font-size: 12px;
            flex-direction: column;
            padding-top: 7px;
            padding-bottom: 1.5rem;
            padding-left: 0;
            p {
                margin-bottom: .5rem;
            }
       }
    }
}

